___CSS_LOADER_EXPORT___.push([module.id, `.chat-container.default2 {
    background-color: #2c2c2c;
    color: #ffffff;
    border: 1px solid #444444;
    border-radius: 12px;
    padding: 10px;
    width: 400px;
    height: 500px;
    display: flex;
    flex-direction: column;
}

.chat-messages {
    flex: 1;
    overflow-y: auto;
    padding: 5px;
}

.chat-bubble {
    max-width: 75%;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 12px;
    word-wrap: break-word;
}

.chat-bubble.user {
    background-color: #007bff;
    color: #ffffff;
    align-self: flex-end;
}

.chat-bubble.bot {
    background-color: #f1f0f0;
    color: #000000;
    align-self: flex-start;
}

.message-input-container {
    display: flex;
    margin-top: 10px;
    /* border-top: 1px solid #444444; */
    padding-top: 10px;
}

.message-input-field {
    flex: 1;
    padding: 8px;
    border: 1px solid #f1f0f0;
    border-radius: 6px;
    margin-right: 5px;
    /* background-color: #444444; */
    color: #000000;
}

.message-input-button {
    background-color: #1a73e8;
    color: white;
    padding: 8px 12px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
}

.message-input-button:hover {
    background-color: #145db7;
}
