___CSS_LOADER_EXPORT___.push([module.id, `.chat-container.default1 {
    background-color: #ffffff;
    color: #333333;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    padding: 10px;
    width: 100%;
    height: 85vh;
    display: flex;
    flex-direction: column;
}

.chat-messages {
    flex: 1;
    overflow-y: auto;
    padding: 5px;
}

.chat-bubble {
    max-width: 50%;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 10px;
    line-height: 1.5;
    word-wrap: break-word;
}

.chat-bubble.user {
    background-color: #007bff;
    color: white;
    margin-left: auto;
    text-align: right;
}

.chat-bubble.assistant {
    background-color: #f1f0f0;
    color: black;
    margin-right: auto;
    text-align: left;
}

.message-input-container {
    display: flex;
    margin-top: 10px;
    /* border-top: 1px solid #e0e0e0; */
    padding-top: 10px;
}

.message-input-field {
    flex: 1;
    padding: 8px;
    border: 1px solid #e0e0e0;
    border-radius: 4px;
    margin-right: 5px;
}

.message-input-button {
    background-color: #007bff;
    color: white;
    padding: 8px 12px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.message-input-button:hover {
    background-color: #0056b3;
}
